<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="circulation-page"
    :headerHeightVW="headerHeightVW"
    scrollbar

  >
    <van-nav-bar slot="header" id="header" title="流转下级" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group>
      <van-cell title="当前流程：">{{oldflow && oldflow.name}}</van-cell>
    </van-cell-group>

    <van-cell-group>
      <!-- <van-cell title="选择下级流程" is-link clickable @click="handleSelectFlow">
        <span>{{flow && flow.name || '请选择'}}</span>
      </van-cell> -->
      <van-cell title="选择下级处理人" is-link clickable @click="handleSelectUser">
        <span>{{user && user.name || '请选择'}}</span>
      </van-cell>
    </van-cell-group>

    <template slot="blank">
      <van-popup 
        v-model="flowShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择下级流程
        </div>
        <a-scroll ref="filterScroller" v-if="flowShow" class="filter-scroll">
          <van-radio-group v-model="flowRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="flowRadio = {...item}" v-for="(item,index) in flowOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onFlowSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup 
        v-model="userShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择下级处理人
        </div>
        <a-scroll ref="filterScroller" v-if="userShow" class="filter-scroll">
          <van-radio-group v-model="userRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="userRadio = {...item}" v-for="(item,index) in userOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
                <img class="user-icon" :src="item.avator"  slot="icon">
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onUserSelect">确定</van-button>
        </div>
      </van-popup>

      <div class="footer-action">
        <van-button :disabled="!user || !user.id" class="action-item-main" color="#108EE9" @click="handleConfirm">确认</van-button>
      </div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import io from '../../api'
import navigate from '../../utils/navigate.js'
import AScroll from '../../components/Scroll'
import db from '../../db'
import {uploadImage, uploadVideo} from '../../utils/upload.js'

export default {
  components: {
    Page,
    AScroll
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    flow: null,
    oldflow: null,
    flowShow: false,
    flowRadio: null,
    flowOptions: JSON.parse(localStorage.getItem('problem_flow')),
    user: null,
    userShow: false,
    userRadio: null,
    userOptions: null,
    job: '',
    profession: '',
    status: ''
  }),
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.id = navigate.getParam('id')
      this.oldflow = JSON.parse(navigate.getParam('flow'))
      this.job = navigate.getParam('job')
      this.profession = navigate.getParam('profession')
      this.status = navigate.getParam('status')

      localStorage.setItem('circulation_select', '')
    },
    onShow () {
      if (localStorage.getItem('circulation_select')) {
        this.user = JSON.parse(localStorage.getItem('circulation_select'))
      } else {
        this.user = null
      }
    },
    onClickLeft () {
      navigate.back()
    },

    handleSelectFlow () {
      this.flowShow = true
      this.flow ? (this.flowRadio = {...this.flow}) : (this.flowRadio = {})
    },

    onFlowSelect () {
      this.flow = {...this.flowRadio}
      this.flowShow = false
    },

    handleSelectUser () {
      // if (this.userOptions) {
      //   this.userShow = true
      //   this.user ? (this.userRadio = {...this.user}) : (this.userRadio = {})
      // } else {
      //   this.$toast.loading({
      //     duration: 0,
      //     loadingType: 'spinner',
      //     message: '获取数据...'
      //   })

      //   io.getOrganization().then(data => {
      //     this.userOptions = data.users

      //     this.$toast.clear()

      //     this.userShow = true
      //     this.user ? (this.userRadio = {...this.user}) : (this.userRadio = {})
      //   }).catch(msg => {
      //     this.$toast('人员获取失败，请稍后重试！')
      //   })
      // }

      navigate.open('organization', {id: this.id, mode: 'problem_circulation', job: this.job, profession: this.profession, status: this.status})
    },

    onUserSelect () {
      this.user = {...this.userRadio}
      this.userShow = false
    },

    handleConfirm () {
      this.$dialog.confirm({
        message: `是否确认流转该问题给${this.user.name}`
      }).then(() => {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner',
          message: '正在流转...'
        })

        // 提交数据
        if (api.connectionType == 'none') {
          alert('已自动保存数据，请在网络环境下提交')
          this.$toast.clear()
        } else {
          if (this.job == 'senior_engineer_business' || this.job == 'engineer_duty') {
            io.passProblem({id: this.id, userId: this.user.id}).then(() => {
              this.$toast.clear()
              
              localStorage.setItem('redirect', 'problemRecord')

              navigate.back('follow')
              navigate.back('circulation')
            }).catch(msg => {
              this.$toast(msg)
            })
          } else {
            db.getProblemDetail(this.id).then(data => {
            
              // 上传图片视频
              uploadImage(data.images).then(s => {
                data.images = s

                uploadVideo(data.videos).then(v => {
                  data.videos = v

                  io.submitProblemData({
                    id: data.id,
                    level: data.level.id,
                    category: data.category.id,
                    analysis: data.analysis,
                    beginSolutions: data.beginSolutions,
                    finishSolutions: data.finishSolutions,
                    remark: data.remark || '',
                    images: data.images,
                    videos: data.videos,
                    noRepairTime: data.noRepairTime,
                    contractor: data.contractor ? data.contractor.id : '',
                    contractorReplyTime: data.contractorReplyTime ? data.contractorReplyTime.substr(0, 16)+':00' : '',
                    contractorBeginTime: data.contractorBeginTime ? data.contractorBeginTime.substr(0, 16)+':00' : '',
                    contractorFinishTime: data.contractorFinishTime ? data.contractorFinishTime.substr(0, 16)+':00' : '',
                    repairFinishTime: data.repairFinishTime ? data.repairFinishTime.substr(0, 16)+':00' : '',
                    contractorNoticeTime: data.contractorNoticeTime ? data.contractorNoticeTime.substr(0, 16)+':00' : '',
                    line: data.line ? data.line.id : '',
                    station: data.station ? data.station.id : '',
                    address: data.address,
                    profession: data.profession ? data.profession.id : '',
                    deviceCategory: data.deviceCategory ? data.deviceCategory.id : '',
                    source: data.source ? data.source.id : ''
                  }).then(() => {
                    io.flowProblem({id: this.id, userId: this.user.id, images: data.images, videos: data.videos}).then(() => {
                      this.$toast.clear()
                      
                      localStorage.setItem('redirect', 'problemRecord')

                      navigate.back('follow')
                      navigate.back('circulation')
                    }).catch(msg => {
                      this.$toast(msg)
                    })
                    
                  }).catch(msg => {
                    this.$toast(msg)
                  })
                })
              })
            })
          }
        }
      })
    }
  }
}
</script>

<style lang="scss">
.circulation-page{
  .van-cell-group+.van-cell-group{
    margin-top: 12px;
  }

  .user-icon{
    width: 45px;
    height: 45px;
    margin-right: 12px;
    border-radius: 50%;
  }

  .filter-popup{
    width: 100%;
    height: 90%;

    .title{
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 16px;
      border-bottom: 1px solid #EBEDF0;
    }

    .filter-scroll{
      height: calc(100% - 110px);
      background: #F7F8FA;
    }
  }
}
</style>
